/* The SIMPLE-TOAST - position it at the bottom and in the middle of the screen */
#simpleToast {
  visibility: hidden; /* Hidden by default. Visible on click */
  min-width: 15rem; /* Set a default minimum width */
  margin-left: -7rem; /* Divide value of min-width by 2 */
  background-color: #fabd9f; /* Background color */
  color: #1f1f1f; /* White text color */
  text-align: center; /* Centered text */
  border-radius: 0.125rem; /* Rounded borders */
  padding: 16px; /* Padding */
  position: fixed; /* Sit on top of the screen */
  z-index: 9991; /* Add a z-index if needed */
  left: 50%; /* Center the snackbar */
  bottom: 30px; /* 30px from the bottom */
  font-family: monospace;
  display: inline-flex;
  line-height: 12px
  

}
#simpleToast span {
  margin-left: 0.75rem;
  margin-top: 0.125rem;
}

/* Show the SIMPLE-TOAST when clicking on a button (class added with JavaScript) */
#simpleToast.show {
  visibility: visible; /* Show the SIMPLE-TOAST */
  /* Add animation: Take 0.5 seconds to fade in and out the SIMPLE-TOAST.
  However, delay the fade out process for 2.5 seconds */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
  // -webkit-animation: fadein 0.5s;
  // animation: fadein 0.5s;
}

/* Animations to fade the SIMPLE-TOAST in and out */
@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 1.9rem; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 1.9rem; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 1.9rem; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 1.9rem; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}
